.filter_items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.mix {
    display: none;
    flex-basis: 32%;
}

.product {
    flex-basis: 32%;
    padding: 20px 15px;
    background: #FFFFFF;
    box-shadow: 0px 6px 16px #E6EBF5;
    transition: all .3s;
    margin-bottom: 20px;

    &:hover {
        box-shadow: 0px 6px 5px #E6EBF5;
    }

    &__img {
        margin-bottom: 10px;
        text-align: center;
        display: block;
    }

    &__title {
        font-size: 18px;
        font-weight: 600;
        color: $fontd;
        margin-bottom: 20px;
        text-align: center;
        display: block;

        &:hover {
            color: $blue;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

    }

    .pinfo {
        display: flex;
        justify-content: space-between;
        padding: 7px 0;
        border-bottom: 1px solid #ececec;

        a {
            text-decoration: underline;
        }

        &__title {
            font-size: 14px;
            color: $fontl;
        }
    }

    &__delivery {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
    }

    &__delivery-item {
        margin-bottom: 7px;
        color: $fontd;
        font-size: 14px;
    }

    .ic_product {
        width: 18px;
        height: 16px;
        margin-right: 10px;
    }

    &__prices {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
    }

    &__price {
        font-family: "Akrobat", sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        color: #4ABE4E;
        margin-right: 10px;
    }

    &__old-price {
        font-size: 12px;
        display: flex;
        align-items: center;
        text-decoration-line: line-through;
        color: $fontl;

    }

    &__btn {
        background: $blue;
        color: #fff;
        border-radius: 4px;
        font-size: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 17px 10px;
        cursor: pointer;
        font-weight: 500;
        margin: 0 20px 20px 20px;
        transition: all .3s;

        &:hover {
            background: $green;
        }


        .ic_btn {
            width: 18px;
            height: 21px;
            margin-left: 10px;
            fill: #fff;
        }
    }

    &__link {
        font-weight: 700;
        color: $blue;
        text-decoration: underline;
        text-align: center;
        display: block;

        &:hover {
            color: $green;
        }
    }
}

@media only screen and (max-width : 691px) {
    .product {
        flex-basis: 49%;
        padding: 15px 15px;
        background: #FFFFFF;
        box-shadow: 0px 6px 16px #E6EBF5;
        transition: all .3s;
        margin-bottom: 20px;


        &__img {
            margin-bottom: 5px;
        }

        &__title {
            font-size: 12px;
            margin-bottom: 10px;
            line-height: 1.4;
        }

        &__info {
            margin-bottom: 15px;
        }

        .pinfo {
            padding: 4px 0;

            a {
                text-decoration: underline;
            }

            &__title {
                font-size: 10px;
            }

            &__info {
                font-size: 10px;

            }
        }

        &__delivery {
            margin-bottom: 15px;
        }

        &__delivery-item {
            margin-bottom: 7px;
            color: $fontd;
            font-size: 12px;
        }

        .ic_product {
            display: none;
        }

        &__prices {
            margin-bottom: 15px;
        }

        &__price {
            font-size: 14px;
        }

        &__old-price {
            font-size: 12px;

        }

        &__btn {

            font-size: 12px;
            padding: 6px 10px;
            margin: 0 0 15px 0;

            &:hover {
                background: $green;
            }


            .ic_btn {
                width: 18px;
                height: 21px;
                margin-left: 10px;
                fill: #fff;
            }
        }

        &__link {
            font-size: 12px;

            &:hover {
                color: $green;
            }
        }
    }

}
