.slider-wrapper {
    display: flex;
    overflow: hidden;
    max-height: 700px;


    .slider-thumb {
        li {
            padding: 10px;
            border: 1px solid #ececec;
            margin-bottom: 5px;
            margin-right: 0px;
            height: 113px;
            width: 113px;
            position: relative;


            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

        }



        .slick-prev3 {
            left: unset;
            top: 0.5em;
        }

        .slick-nextt3 {
            top: unset;
            right: unset;
            bottom: -0.5em;
        }

        .slick-prev3,
        .slick-nextt3 {
            z-index: 1;
            height: 1em;
            width: 100%;
            background-color: #000;
            opacity: 0.3;
            transition: opacity 300ms ease;
            font-size: 1.5em;
            color: #fff;
            border: none;

            &::before {
                content: '';

            }

            &::hover {
                opacity: 0.5;


            }


        }
    }

    .slider-preview {

        li {
            padding: 10px;
            width: 514px;
            height: 518px;
            border: 1px solid #ececec;
            margin-left: 5px;

            a {
                outline: none;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;


            }
        }


    }

}











