@media only screen and (max-width: 767px) {
    * {
        min-height: 0;
        min-width: 0;
    }




    .slider-wrapper {
        display: flex;
        overflow: hidden;
        max-height: 800px;
        flex-direction: column-reverse;




        .slider-thumb {
            max-width: 100%;

            li {
                padding: 3px;
                border: 1px solid #ececec;
                margin-bottom: 5px;
                margin-right: 5px;
                max-height: 100px;




                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }


            }



            .slick-prev3 {

                top: 0.5em;
                left: 0;

                .fa {
                    &::before {
                        content: "\f104";
                    }
                }
            }

            .slick-nextt3 {
                top: 0.5em;
                right: 0;

                .fa {
                    &::before {
                        content: "\f105";
                    }
                }
            }

            .slick-prev3,
            .slick-nextt3 {
                top: 0;
                width: 1em;
                height: 100px;
                border: none;
                position: absolute;





            }
        }

        .slider-preview {
            li {
                padding: 0px;
                border: 1px solid #ececec;
                margin-left: 0px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;

                }
            }


        }

    }
}

