//прижимаем футер

.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}


hr {
    background-color: #fff;
}

.footer {
    margin-top: auto;
    padding: 40px 0;
    background: #232F3E;

    &__logos {
        img {
            margin-bottom: 15px;
        }

    }

    &__social {
        margin-top: 30px;
        display: flex;
        opacity: .9;
        margin-bottom: 20px;

        a {
            color: #fff;
            font-size: 16px;
        }

        .ic_social {
            width: 25px;
            height: 25px;
            fill: #fff;
            margin-right: 10px;
        }

        &:hover {
            opacity: 1;
        }
    }

    &__socialtext {
        color: #fff;
        opacity: .5;
    }

    &__name {
        font-family: 'Akrobat', sans-serif;
        color: #fff;
        font-size: 24px;
        margin-bottom: 15px;
        font-weight: 600;
        text-transform: uppercase;

    }

    &__descriptor {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: #66727F;

    }


    &__title {
        color: #fff;
        font-size: 18px;
        margin-bottom: 30px;
        font-weight: 600;
        text-transform: uppercase;
    }

    &__phones {
        margin-bottom: 30px;
    }

    &__phone {
        display: block;
        color: #fff;
        font-size: 18px;
        margin-bottom: 10px;
        font-weight: 600;

    }

    &__ws {
        margin-bottom: 20px;

        a {
            font-size: 16px;
            color: #fff;
            opacity: .5;

            &:hover {
                opacity: 1;
            }
        }
    }

    &__contact {
        display: flex;
        margin-bottom: 20px;


        a {
            font-size: 16px;
            color: #fff;
            opacity: .5;
            margin-bottom: 15px;
            display: block;
            line-height: 1.4;

            &:hover {
                opacity: 1;
            }

        }
    }

    &__ic {
        width: 17px;
        height: 17px;
        fill: $green;
        margin-right: 10px;
        margin-top: 5px;
    }

    &__menu {
        margin-bottom: 30px;

        a {
            font-size: 16px;
            color: #fff;
            opacity: .5;
            margin-bottom: 15px;
            display: block;
            line-height: 1.4;

            &:hover {
                opacity: 1;
            }

        }
    }

    &__info {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__privacy {
        flex-basis: 50%;
        font-size: 12px;
        color: #fff;
        opacity: .5;
        line-height: 1.4;
    }

    &__delosait {
        flex-basis: 20%;

        a {
            font-size: 12px;
            opacity: .5;
            color: #fff;

            &:hover {
                opacity: 1;
            }
        }
    }
}

.payment {
    display: flex;

    &__item {
        margin-right: 11px;
    }
}

@media only screen and (max-width : 560px) {
    .footer {
        &__privacy {
            flex-basis: 100%;
            margin-bottom: 20px;
        }

        &__delosait {
            flex-basis: 100%;
            margin-bottom: 20px;
        }

        &__title {
            margin-bottom: 20px;
            font-size: 16px;
        }

        &__menu {

            a {
                font-size: 14px;
                line-height: 1.3;
            }
        }

        &__phone {
            font-size: 16px;
        }

        &__contact {
            a {
                font-size: 14px;
                margin-bottom: 0px;
            }
        }

        &__ic {
            margin-top: 0;
        }



    }

}
