.products {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}


.ui-radiobox_filter {
    font-weight: 300;
    font-size: 14px;
    color: #747474;
}

.ui-opt_filter {
    margin-top: 0 !important;
}

.catalog-sort_filter {
    padding: 0;
}

.btn--or {
    background: $blue;
    z-index: 60;
    position: relative;
    margin: 20px 0;
    padding: 7px;
    position: fixed;
    right: 0;
    top: 40%;
    border: none;

    .filtr {
        fill: #fff;
        width: 15px;
        height: 15px;
    }
}

.myfilter {
    background: #fff;
    margin-top: 20px;
    margin-bottom: 20px;

}

.ic_filter {
    width: 20px;
    height: 20px;
    fill: #fff;
    margin: 0px;
}

.filter_title {
    display: block;
    font-size: 15px;
    font-weight: 700;
    color: $fontd;
    border-bottom: 1px solid #eee;
    padding-bottom: 15px;
    margin-bottom: 15px;
    padding: 15px;
}

.filters__block {
    border-bottom: 1px solid #eee;
    padding: 15px;
    margin-bottom: 0;
    position: relative;

    .js-accordion-content {
        .ui-range__col {
            span {
                z-index: 10;
            }
        }
    }
}

.checkbox-text {
    font-weight: 300;
    color: #747474;
}

.filters_btns {
    display: flex;
    justify-content: flex-start;
    padding: 20px 0;
    margin-left: 15px;

    .filter_show {
        border: 1px solid $blue;
        background: $blue;
        transition: all .3s;
        color: #fff;
        font-size: 14px;
        padding: 15px 20px;

        &:hover {
            border: 1px solid $blue;
            background: transparent;
            transition: all .3s;
            color: $blue;
            font-size: 14px;
            padding: 15px 20px;
        }
    }

    .filter_reset {
        &:hover {
            border: 1px solid $blue;
            background: $blue;
            transition: all .3s;
            color: #fff;
            font-size: 14px;
            padding: 15px 20px;
        }

        border: 1px solid $blue;
        background: transparent;
        transition: all .3s;
        color: $blue;
        font-size: 14px;
        padding: 15px 20px;
    }
}

.filter_show {
    margin-right: 20px;
}



.filters {
    .btn-close {
        display: none;
    }
}

.filters__label {
    font-size: 14px;
    line-height: 1.3;
    font-weight: 600;
    margin-bottom: 20px;

}

.filters-check {

    display: flex;
    width: 100%;
    margin: 0;
    cursor: pointer;

    &:not(:last-child) {
        margin-bottom: 15px;
    }

    .checkbox-wrap {

        display: flex;

        align-items: center;

        justify-content: center;
        width: 20px;
        max-width: 20px;

        flex: 0 0 20px;
        height: 20px;
        border-radius: 2px;
        border: 2px solid #e7e7e7;

        &::after {
            content: '';
            display: block;
            width: 12px;
            height: 10px;
            background: url("../img/svg-check-ic.svg") no-repeat 0 0;
            background-size: 100%;
            will-change: transform;

            transform: scale(0);
            opacity: 0;

            transition: all 0.1s linear;
        }
    }

    .checkbox-text {
        display: inline-block;
        margin-top: 1px;
        margin-left: 14px;
        font-size: 14px;
    }

    input {
        &:checked {
            + {
                .checkbox-wrap {
                    &::after {

                        transform: scale(1);
                        opacity: 1;
                    }
                }
            }
        }
    }
}

.filters-row {

    display: flex;

    flex-wrap: wrap;
}

.filters-row-col2 {
    .filters-check {
        width: 50%;
        padding-right: 15px;
    }
}

.filters__btns {
    .btn {

        display: flex;
        width: 100%;

        &:not(:last-child) {
            margin-bottom: 12px;
        }
    }
}

.filters__reset {
    font-size: 14px;
    border-color: #e7e7e7;

    &:hover {
        opacity: 0.8;
        border-color: #c5c5c5;
    }
}

.filters-modal__title {
    display: none;
}

.filter-tooltip {
    position: absolute;
    left: 100%;
    top: 50%;
    min-width: 288px;

    transform: translateY(-50%);
    display: flex;
    align-items: center;
    padding: 15px 25px;
    background-color: #fff;

    box-shadow: 0 0 18px rgba(0, 0, 0, 0.07);
    z-index: 2;

    &::before {
        content: '';
        display: block;
        width: 10px;
        height: 10px;
        position: absolute;
        left: -5px;
        top: 50%;
        margin-top: -7px;
        background: #fff;

        transform: rotate(45deg);
    }

    .btn {
        width: 133px;
        height: 54px;
    }
}

.filter-tooltip__txt {

    display: flex;
    font-size: 15px;
    font-weight: 700;
    margin-right: 25px;
}

.filter-tooltip__num {
    display: inline-block;
    margin-left: 4px;
}

.js-accordion-btn {
    cursor: pointer;
    position: relative;
    padding-left: 20px;

    &::before {
        content: '';
        display: block;
        width: 5px;
        height: 9px;
        position: absolute;
        left: 2px;
        top: 50%;
        margin-top: -4px;
        z-index: 0;
        background: url("../img/ic-arrow.svg") no-repeat;
        background-size: 100%;

        transform: rotate(90deg);

        transition: all 0.1s linear;
    }
}

.js-accordion-btn.active {
    &::before {

        transform: rotate(-90deg);
    }
}

.js-accordion-content {
    display: block;
}

.input-block {
    position: relative;

    &:not(:last-of-type) {
        margin-bottom: 16px;
    }

    &:not(.input-search) {
        .input {
            &:focus {
                padding: 19px 15px 6px;

                + {
                    .input-placeholder {
                        opacity: 1;
                        top: 7px;
                    }
                }
            }
        }

        .input.populated {
            padding: 19px 15px 6px;

            + {
                .input-placeholder {
                    opacity: 1;
                    top: 7px;
                }
            }
        }
    }
}

.checkbox {

    display: flex;
    cursor: pointer;
    margin-bottom: 0;

    .checkbox-wrap {

        display: inline-flex;

        justify-content: center;

        align-items: center;
        width: 17px;
        height: 17px;

        flex: 0 0 17px;
        background-color: #f7f7f7;

        &::before {
            content: '';
            display: block;
            width: 9px;
            height: 9px;
            background: #2dc964;

            transform: scale(0);

            transition: all .1s;
        }
    }

    .checkbox-text {
        display: inline-block;
        margin-left: 8px;
        opacity: 0.5;
    }

    input {
        &:checked {
            + {
                .checkbox-wrap {
                    &::before {

                        transform: scale(1);
                    }
                }
            }
        }
    }
}

.checkbox-hidden {
    display: none;
}

.catalog-controls {

    display: flex;
    align-items: center;
    margin-bottom: 25px;
    font-weight: 500;
    position: relative;
    z-index: 2;
}

.catalog-controls__filters {
    display: none;
    margin-right: 30px;

    .btn {
        padding: 0 .5em;
        height: 44px;

        &::before {
            content: '';
            display: block;
            width: 22px;
            height: 22px;
            margin-right: 4px;
            background: url("/img/ic-filters.svg") no-repeat 0 0;
            background-size: 100%;
        }
    }

    .filters-quantity {
        display: inline-block;
        margin: 0 5px 0 8px;
        color: #000;
    }
}


@media screen and (max-width: 1199px) {
    .catalog-page__aside {
        flex: 0 0 100%;
        max-width: 100%;
        margin-right: 0;

        .filters {
            display: none;
        }
    }

    .filters-modal {
        max-width: 500px;
        margin: 0 auto;
        padding: 30px;
        background: #fff;

        box-shadow: 0 0 68px rgba(0, 0, 0, 0.08);
        position: relative;

        .btn-close {
            display: block;
            width: 15px;
            height: 15px;
            top: 5px;
            right: 5px;





        }

        .color-pallete {
            max-width: 100%;
        }

        .filters-row {
            .filters-check {
                width: 50%;
                padding-right: 15px;
            }
        }

        .filters-row-col2 {
            .filters-check {
                width: 33.33333%;
            }
        }
    }

    .filters-modal__title {
        display: block;
    }

    .filter-tooltip {
        display: none;
    }

    .catalog-controls__filters {
        display: block;
    }
}

@media screen and (max-width: 991px) {
    .filters-modal {
        max-width: 400px;
    }
}

@media screen and (max-width: 575px) {
    .filters-modal {
        padding: 30px 15px;

        .filters-row {
            .filters-check {
                width: 100%;
                padding-right: 0;
            }
        }

        .filters-row-col2 {
            .filters-check {
                width: 50%;
                padding-right: 15px;
            }
        }
    }

    .catalog-controls {

        flex-wrap: wrap;
    }

    .catalog-controls__filters {
        width: 100%;
        margin: 0 0 20px;
    }
}

@media screen and (max-width: 767px) {
    .catalog-controls__filters {
        margin-right: 15px;
    }
}




.checkbox {

    display: flex;
    cursor: pointer;
    margin-bottom: 0;
}

.checkbox .checkbox-wrap {

    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 17px;
    height: 17px;
    flex: 0 0 17px;
    background-color: #f7f7f7;
}

.checkbox .checkbox-wrap::before {
    content: '';
    display: block;
    width: 9px;
    height: 9px;
    background: #1E1D2B;
    transform: scale(0);
    transition: all .1s;
}

.checkbox .checkbox-text {
    display: inline-block;
    margin-left: 8px;
    opacity: 0.5;
}

.checkbox input:checked + .checkbox-wrap::before {
    transform: scale(1);
}

.submit {
    height: 54px;
    margin-top: 20px;
}

.flex {
    display: flex;
    flex-wrap: wrap;
}

.ui-radiobox,
.ui-toggle {
    display: block;
    margin-bottom: 0;
    cursor: pointer;
}

.ui-radiobox__label {
    display: flex;
    align-items: center;
    position: relative;
    line-height: 1.4;



}

.ui-radiobox__label::before,
.ui-radiobox__label::after {
    content: '';
    display: block;
    border-radius: 50%;
}

.ui-radiobox__label::before {
    width: 27px;
    flex: 0 0 27px;
    height: 27px;
    margin-right: 16px;
    background: #fff;
    border: 2px solid #e7e7e7;
}

.ui-radiobox__label::after {
    width: 13px;
    height: 13px;
    position: absolute;
    top: 7px;
    left: 7px;
    background: #1E1D2B;
    will-change: transform;
    transform: scale(0);
    transition: all 0.1s linear;
}

.ui-radiobox input:checked + .ui-radiobox__label::after {
    transform: scale(1);
}

.ui-radiobox:not(:last-child) {
    margin-bottom: 13px;
}

.input-hidden {
    display: none;
}

select {
    border: 1px solid lightgray;
    padding: 10px 15px;
    color: gray;
    outline: none;
}
