.services {
    padding: 90px 0;
}

.arrows {
    margin-top: 20px;
}

.pr-prev,
.pr-next {
    width: 50px;
    height: 50px;
    position: relative;
    background: $blue;
    border: 0;
    border-radius: 4px;
    margin: 3px;
    transition: all .3s;

    &:hover {
        background: $green;
    }

    &::after {
        content: '';
        display: block;
        width: 10px;
        height: 10px;
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        top: 0;
        bottom: 0;

    }
}

.pr-next {

    &::after {
        background: url(../img/pr-next.svg);
        background-repeat: no-repeat;

    }
}

.pr-prev {

    &::after {
        background: url(../img/pr-prev.svg);
        background-repeat: no-repeat;

    }
}


@media only screen and (max-width : 667px) {
    .services {
        padding: 30px 0;
    }

}
