.slider {
    margin-right: -50%;

}

.slider__item {
    padding: 40px 20px;
    height: 320px;
    border-radius: 4px;
    overflow: hidden;
    position: relative;
    background: #FFFFFF;
    box-shadow: 0px 6px 16px #E6EBF5;
    border-radius: 8px;
    margin-right: 20px;
    margin-top: 20px;
    margin-bottom: 20px;


}

.slider__img {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 1;
}

.slider__title {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 20px;
    color: $fontd;
    position: relative;
    z-index: 20;
}

.slider__info {
    color: $fontl;

    ul {
        list-style-type: disc;
        line-height: 1.5;
        margin-left: 20px;
        margin-bottom: 20px;
        font-size: 16px;
        position: relative;
        z-index: 20;
    }

}

.slider__more {
    font-weight: 600;
    color: $blue;
    font-size: 16px;

    &:hover {
        color: $green;
    }

}

@media only screen and (max-width : 1200px) {
    .slider {
        margin-right: 0%;

    }

}
