.brands {
    margin-bottom: 60px;
}

.items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-top: 1px solid #ececec;
    border-left: 1px solid #ececec;
}

.item {
    flex-basis: 20%;
    border-bottom: 1px solid #ececec;
    border-right: 1px solid #ececec;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px;
    transition: all .3s;

    &:hover {
        box-shadow: 5px 5px 20px #ececec;
    }

    span {
        align-self: center;


        img {
            max-width: 100%;
        }
    }
}

.show-more {
    background: #f7f7f7;
    border: none;
    color: $fontl;
}

@media only screen and (max-width : 691px) {
    .brands {
        margin-bottom: 30px;
    }

    .item {
        flex-basis: 33%;
    }
}
