.content {
    background: #fff;
    padding: 30px;
    margin-top: -60px;
    box-shadow: 0 0 20px #00000014;

    &__banner {
        padding-bottom: 20px;
        border-bottom: 3px solid $blue;

        /*    img {
        margin-top: 30px;
    }*/

    }

    &__bannerinf {
        h2 {
            color: $blue;
            font-family: 'Akrobat';
            font-size: 38px;
            font-weight: 600;
            margin: 20px 0;
            text-transform: uppercase;
        }
    }

    &__info {
        margin-top: 20px;

        h3 {
            margin: 20px 0;
            font-size: 26px;
            font-weight: 700;
        }

        .main-txt {
            padding: 20px 10px 30px 30px;
            margin: 20px 0;
            border-left: 3px solid $blue;
            background: #f6f9fc;

        }

        p {
            margin: 10px 0;
        }

        .bottom-aboutwrp {
            margin-top: 30px;
        }

        .bottom-about2 {
            text-align: end;
            font-size: 16px;
            opacity: .7;
            font-style: italic;
            margin-top: 30px;
        }

    }


}

.content--about {
    padding-top: 0;
}


.service_check {
    padding: 20px;
    background: #fff;
    border: 1px solid #ececec;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    margin-top: -60px;
}

/*.ic_serv {
    width: 70px;
    height: 70px;
    fill: $blue;
    flex-basis: 7%;
}*/

.service_check_text {
    line-height: 1.5;
    margin: 0 20px;
    flex-basis: 63%;
    font-size: 14px;
}

.btn_check {
    flex-basis: 30%;
    text-align: center;
}



article {
    padding: 30px 0;



    h3 {
        font-size: 22px;
        font-weight: 600;
        line-height: 1.5;

    }

    p {
        line-height: 1.5;
        padding: 5px 0;
    }

    .text_info {
        background: #fafafa;
        padding: 20px 15px 20px 40px;
        border-left: 3px solid $blue;
        line-height: 1.5;
        margin: 20px 0;


    }

    ul,
    ol {
        padding: 5px 10px 5px 40px;

        line-height: 1.5;
        list-style-type: disc;

        li {
            padding: 5px 0;

        }
    }

    ol {
        list-style-type: decimal;

        li {
            padding-left: 7px;
        }
    }
}

@media only screen and (max-width : 1200px) {
    .service_check {
        flex-wrap: wrap;
    }

    .ic_serv {
        display: none;
    }

    .service_check_text {

        flex-basis: 100%;
        margin-bottom: 20px;
        margin-right: 0;
        margin-left: 0;
    }

    .btn_check {
        flex-basis: 100%;
        margin-bottom: 20px
    }


}


@media only screen and (max-width : 992px) {
    .order1 {
        order: 2;

    }

    .order2 {
        order: 1;

    }


}

@media only screen and (max-width : 768px) {
    article {
        padding: 0 0;
    }

    .content--about {
        padding-top: 20px;
    }

    .content {
        padding: 15px;

        &__info {
            h3 {
                font-size: 20px;
            }
        }

        &__bannerinf {
            h2 {
                font-size: 20px;
            }
        }
    }


}
