.header_top {
    top: -220px;
}

.fixed {
    transition: top 1s;
    width: 100%;
    position: fixed;
    top: 0;
    background: #fff;
    z-index: 60;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 1320px;
    padding: 0 15px;
    z-index: 100;




    &::before {
        content: "";
        width: 3000px;
        height: 105px;
        background: #fff;
        position: absolute;
        top: 0;
        left: -1000px;
        display: block;
        z-index: -1;
    }


}

.mfixed {

    margin-top: 180px;
}

.pfixed {

    margin-top: 220px;
}

@media only screen and (max-width : 1200px) {
    .fixed {
        padding: 0px 15px;

        &::before {
            height: 30px;


        }
    }
}

@media only screen and (max-width : 576px) {
    .fixed {
        padding: 5px 15px;

        &::before {
            height: 30px;


        }
    }

    .header-wrap {
        padding-bottom: 0px;
    }

    .toggle-mnu {
        margin-top: 10px;
    }
}
