.header_main {
    background: url('../img/bg1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    padding: 5px 0;
    height: 760px;
}

.header_page {
    height: 170px;
    background: #fff;
}

.header_top {
    display: flex;
    flex-wrap: wrap;


}

.logo {
    flex-basis: 30%;
    display: flex;
    align-items: center;


    &__img {
        margin-right: 30px;
        width: 101px;

    }

    &__descr {
        display: flex;
        flex-direction: column;

        .logo-title {
            font-family: 'Akrobat', sans-serif;
            font-size: 26px;
            font-weight: 700;
            line-height: 32px;
            margin-bottom: 4px;
            color: $fontd;

            span {
                color: $blue;
            }
        }

        .descriptor {

            font-size: 16px;
            font-style: normal;
            font-weight: 500;
            line-height: 19px;
            letter-spacing: 0em;
            text-align: left;
            color: $fontl;

        }
    }

    &:hover {
        text-decoration: none;
        color: inherit;
    }
}

.adress {
    flex-basis: 25%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 40px;


    &__item {
        display: flex;
        margin-bottom: 10px;
        color: $fontl;

        &:last-child {
            margin-bottom: 0;
        }

        a {
            color: $fontl;

            &:hover {
                color: $blue;
            }
        }
    }

    .ic_header {
        width: 15px;
        height: 15px;
        margin-right: 10px;
        fill: $blue;
    }
}

.number {
    flex-basis: 20%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;

    &__tel {
        color: $fontd;
        font-size: 22px;
        font-style: normal;
        font-weight: 700;
        line-height: 26px;
        letter-spacing: 0em;
        text-align: right;
        margin-bottom: 10px;


    }

    &__ws {
        color: $fontl;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 19px;
        letter-spacing: 0em;
        text-align: left;

        &:hover {
            color: $blue;
        }

        .ic__ws {
            width: 14px;
            height: 14px;
        }

    }

}

.btn_wrp {
    flex-basis: 25%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.btn-green {

    background: linear-gradient(180deg, #51CC56 0%, #42AD46 100%);
    box-shadow: 0px 8px 20px #47B84C, 0px 4px 0px #3DA440, inset 0px -8px 15px #43C248;
    border-radius: 4px;
    color: #fff;
    font-family: 'Akrobat', sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    letter-spacing: 0em;
    display: flex;
    text-transform: uppercase;
    padding: 20px 40px;
    max-width: 240px;
    transition: all .3s;
    cursor: pointer;

    &:hover {
        box-shadow: 0px 3px 5px #47B84C, 0px 4px 0px #3DA440, inset 0px -8px 15px #43C248;
        color: #fff;
        text-decoration: none;

    }



}

.header__menu {
    flex-basis: 100%;
    position: relative;


    &::after {
        content: "";
        width: 4000px;
        height: 1px;
        background: #CFDBE8;
        position: absolute;
        top: 5px;
        left: -900px;
        display: block;
        z-index: 10;
    }
}


.fixed {
    .logo {

        &__img {
            margin: 7px 10px 7px 0;
            width: 50px;
            height: 50px;
        }

        &__descr {
            display: flex;
            flex-direction: column;

            .logo-title {
                font-size: 22px;
                line-height: 1px;
                margin-bottom: 15px;
            }

            .descriptor {
                font-size: 12px;
                line-height: 1;
            }
        }
    }

    .adress {
        margin-top: 5px;

        &__item {
            margin-bottom: 10px;
            font-size: 14px;
        }

        .ic_header {
            width: 12px;
            height: 12px;
            margin-right: 10px;
        }
    }

    .number {
        &__tel {
            font-size: 18px;
            line-height: 1;
            margin-bottom: 6px;
        }

        .number__ws {
            font-size: 14px;

        }

    }

    .btn-green {
        font-size: 14px;
        text-transform: uppercase;
        padding: 13px 40px;
        max-width: 240px;
    }

    .sf-menu {
        margin-bottom: 5px;
    }

    .sf-menu li a {
        font-size: 16px;
        padding: 10px 40px 10px 10px;

    }

    .dropdwn:after {
        top: 7px;

    }
}



.heading {
    display: flex;
    padding: 66px 0 186px 0;
}

.heading_wrp {
    flex-basis: 50%;


    h1 {
        font-family: 'Akrobat', sans-serif;
        font-size: 54px;
        font-style: normal;
        font-weight: 700;
        line-height: 67px;
        letter-spacing: 0em;
        text-align: left;
        color: #1C6D9A;
        margin-bottom: 40px;

        span {
            font-size: 52px;
            font-style: normal;
            font-weight: 500;
            line-height: 67px;
            letter-spacing: 0em;
            text-align: left;
            color: $fontd;
            margin-top: 10px;
        }
    }

    &__tizers {
        display: flex;
    }

    .tizer {
        display: flex;
        align-items: center;
    }

    .tizer-icon {
        width: 81px;
        background: rgba(249, 237, 237, 0.34);
        height: 81px;
        border-radius: 50%;
        overflow: hidden;
        position: relative;
        display: flex;
        justify-content: center;


        .tizer-number {
            font-family: 'Akrobat', sans-serif;
            font-style: normal;
            font-weight: 900;
            font-size: 72px;
            line-height: 90px;
            color: #EBEBEB;
            position: absolute;
            top: 10px;

        }
    }

    .tizer-descr {
        flex-basis: 70%;
        font-weight: 600;
        font-size: 18px;
        line-height: 22px;
        color: $fontd;
        margin-left: 15px;

    }

}

.mfixed {
    .heading_wrp__tizers {
        display: none;
    }
}

.mobile_wrp {
    display: none;
}


@media only screen and (max-width : 1200px) {

    .btn_wrp {
        display: none;
    }

    .logo {
        flex-basis: 43%;
    }

    .adress {
        flex-basis: 33%;
    }

    .number {
        flex-basis: 23%;
    }

    .fixed {
        .logo {

            &__img {
                width: 50px;
                height: 50px;
            }

            &__descr {
                display: flex;
                flex-direction: column;

                .logo-title {
                    font-size: 22px;
                    line-height: 1px;
                    margin-bottom: 15px;
                }

                .descriptor {
                    font-size: 12px;
                    line-height: 1;
                    padding-right: 10px;
                }
            }
        }


    }

    .heading_wrp {
        flex-basis: 100%;
    }


}

@media only screen and (max-width : 990px) {
    .header_main {
        background: url('../img/bg1.jpg');
        background-repeat: no-repeat;
        background-size: cover;
        padding: 5px 0;
        height: 560px;
    }

    .header_page {
        height: 64px;
        background: #fff;
    }

    .header_top {
        flex-wrap: nowrap;
    }

    .adress,
    .number {
        display: none;

    }

    .logo {
        flex-basis: 70%;
        align-items: center;

        &__img {
            margin-right: 5px;
            width: 50px;
        }

        &__descr {
            .logo-title {
                font-size: 18px;
                line-height: 1;
            }

            .descriptor {
                font-size: 11px;
                line-height: 1.3;
            }
        }

        &:hover {
            text-decoration: none;
            color: inherit;
        }
    }

    .mobile_wrp {
        display: flex;
        flex-basis: 20%;
        justify-content: flex-end;
    }

    .header__menu {
        flex-basis: 10%;
        display: flex;
        justify-content: flex-end;

        &::after {
            display: none;
        }
    }



}

@media only screen and (max-width : 691px) {
    .header_page {
        height: 50px;
        background: #fff;
    }

    .logo {
        flex-basis: 60%;
        align-items: center;

        &__img {
            width: 30px;
            height: 30px;
        }

        &__descr {
            .logo-title {
                font-size: 16px;
                line-height: 1;
            }

            .descriptor {
                display: none;

            }
        }


    }

    .fixed {
        padding: 10px 15px;

        .logo {
            flex-basis: 60%;
            align-items: center;

            &__img {
                width: 30px;
                height: 30px;
            }


            &__descr {
                .logo-title {
                    font-size: 16px;
                    line-height: 1;
                    margin-bottom: 0px;
                }

                .descriptor {
                    display: none;

                }
            }


        }

        .mobile_wrp {

            flex-basis: 30%;

        }

    }

    .mobile_wrp {

        flex-basis: 30%;

    }

    .heading_wrp {

        h1 {
            font-size: 26px;
            line-height: 1.3;
            margin-bottom: 20px;

            span {
                font-size: 24px;
                line-height: 1.3;
                margin-top: 0px;
            }
        }

        &__tizers {
            display: flex;
            flex-wrap: wrap;
        }

        .tizer {
            margin-bottom: 15px;
        }

        .tizer-icon {
            width: 41px;
            height: 41px;

            .tizer-number {
                font-size: 46px;
                line-height: 1.3;
                color: #1c6d9a45;
                top: 0;

            }
        }

        .tizer-descr {
            flex-basis: 70%;
            font-weight: 600;
            font-size: 14px;
            line-height: 1.3;
            color: $fontd;
            margin-left: 15px;

        }

    }


}
