 .mobile_buttons {
     display: flex;
     justify-content: flex-end;
     align-items: center;

     a {
         background: $blue;
         width: 30px;
         height: 30px !important;
         margin-right: 2px;
         height: auto;
         display: flex;
         justify-content: center;
         align-items: center;
         border-radius: 4px;
     }

     .ic_mob {
         width: 15px;
         height: 15px;
         fill: #fff;
     }
 }
