.blueb {
    background: #FAFCFF;
}

.blocks {
    display: flex;
    margin-top: -70px;

    .block {
        flex-basis: 33.3%;
        padding: 25px 30px;
        color: #fff;
    }

    .first-block {
        background: #1A9BE3;
        box-shadow: 0px 10px 0px -4px #167FBA;
    }

    .second-block {
        background: #168CCD;
        box-shadow: 0px 10px 0px -4px #0D70A7;
    }

    .third-block {
        background: #0A7BBA;
        box-shadow: 0px 10px 0px -4px #066599;
    }

    .block_title {
        font-family: "Akrobat", sans-serif;
        font-style: normal;
        font-weight: 600;
        font-size: 38px;
        line-height: 1.3;
        margin-bottom: 10px;
        text-transform: uppercase;
    }

    .block_subtitle {
        font-family: "Akrobat", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 38px;
        line-height: 1.4;
        text-transform: uppercase;

    }

    ul {
        font-weight: 500;
        font-size: 18px;
        padding-left: 60px;


        li {
            line-height: 1.3;
            margin-bottom: 20px;
            position: relative;

            &:before {
                content: "";
                width: 10px;
                height: 10px;
                background: #FEC218;
                position: absolute;
                left: -30px;
                top: 0;
                bottom: 0;
                margin: auto;
                display: block;
                border-radius: 50%;

            }
        }


    }

    .input_wrp {
        position: relative;

    }

    .ic_input {
        position: absolute;
        height: 24px;
        width: 24px;
        top: 0;
        bottom: 0;
        margin: auto;
        left: 15px;
    }

    .bblocks-input {
        display: block;
        background: #F9EDED;
        height: 71px;
        width: 100%;
        border: none;
        box-shadow: none;
        outline: none;
        padding-left: 50px;
        margin-bottom: 12px;
    }

    .yellow-btn {
        background: linear-gradient(180deg, #FECB00 0%, #FDA400 73.96%);
        box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.4), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 0px #F98300, inset 0px -8px 15px #FDAB0C;
        border-radius: 4px;
        font-family: "Akrobat", sans-serif;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #712D12;
        text-transform: uppercase;
        border: none;
        padding: 25px 40px;
        width: 100%;
        transition: all .3s;

        &:hover {
            box-shadow: 0px 8px 5px rgba(0, 0, 0, 0.4), 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 0px #F98300, inset 0px -8px 15px #FDAB0C;

        }

    }


}

@media only screen and (max-width : 992px) {
    .blocks {
        .block {
            padding: 15px 15px;
            color: #fff;
        }

        .block_title {
            font-size: 26px;
        }

        .block_subtitle {
            font-size: 26px;

        }

        ul {
            font-size: 14px;
            padding-left: 30px;


            li {
                line-height: 1.3;
                margin-bottom: 20px;
                position: relative;

                &:before {
                    left: -25px;


                }
            }


        }

    }

}

@media only screen and (max-width : 667px) {
    .blocks {
        flex-wrap: wrap;

        .block {
            flex-basis: 100%;
        }

    }
}

@media only screen and (max-width : 576px) {
    .blocks {
        margin-top: -170px;

        .block_title {
            font-size: 22px;
        }

        .block_subtitle {
            font-size: 22px;

        }

    }
}
