.title-wrp {
    margin-bottom: 40px;
}

.block-title {
    font-family: "Akrobat", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 42px;
    line-height: 1.3;
    display: flex;
    justify-content: center;
    color: $fontd;
    margin-bottom: 15px;
    text-transform: uppercase;
}

.block-subtitle {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.3;
    display: flex;
    justify-content: center;
    color: $fontl;


}

.block-title--left {
    justify-content: flex-start;
}

.catalog {
    padding: 90px 0;
    background: #FAFCFF;
    position: relative;
    z-index: 10;
}

.catalog-filter {
    background: #FFFFFF;
    box-shadow: 0px 6px 16px #E6EBF5;
    border-radius: 8px;
}

.portfolio-filter {
    margin-bottom: 40px;
}

.filter-title {
    font-size: 18px;
    font-weight: 700;
    color: $fontd;
    padding: 19px 10px 13px 10px;

}

.filter {
    border-bottom: 1px solid #ececec;

    a {
        color: $fontl;
        line-height: 22px;
        display: block;
        padding: 10px 15px;
    }

    .selected {
        color: $blue;
    }
}



.sticky-top {
    top: 110px;
}

/*.portfolio-wrap .sticky-top {
    top: 180px;

}*/

.catalog-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 15px 0;
}

@media only screen and (max-width : 691px) {
    .title-wrp {
        margin-bottom: 20px;
    }

    .sticky_wrp {
        display: flex;
        flex-direction: column;

        .catalog-filter {
            order: 2;

        }

        .catalog-link {
            order: 1;
            margin-bottom: 20px;
        }

    }

    .catalog-filter {
        padding-bottom: 10px;

    }

    .block-title {
        font-size: 24px;
        margin-bottom: 5px;

    }

    .block-subtitle {
        text-align: center;
        font-size: 16px;


    }

    .block-title--left {
        justify-content: center;
    }

    .catalog {
        padding: 30px 0;

    }

    .filter-title {
        text-align: center;
        padding: 10px 10px 10px 10px;

    }

    .cd-filters {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;

    }

    .filter {
        border-bottom: none;

        a {
            color: $fontl;
            line-height: 1.3;
            display: inline-block;
            padding: 3px 5px;
            font-size: 12px;
            border: 1px solid #ececec;
            border-radius: 4px;
            margin: 3px;
        }

        .selected {
            color: $blue;
        }
    }

}
