.page_content {
    background: #fff;
    box-shadow: 5px 5px 20px #6363631c;
    padding: 15px;
    margin-top: -30px;
}

.pc_services_block {
    padding: 10px;
    border: 1px solid #ececec;
    margin-bottom: 30px;
}

.pc_services_block__text {
    line-height: 1.5;
    font-size: 17px;
    margin-bottom: 20px;
}

.pc_services_block__title {
    font-size: 18px;
    margin-bottom: 10px;
    font-weight: 700;
    line-height: 1.4;
}

.serv_link {
    padding: 5px;
}

.pc_services_block__image {
    margin-bottom: 15px;

    img {
        min-height: 97px;
        object-fit: cover;
    }
}
